*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  /* Font varient */
  font-variant-ligatures: none;
  -webkit-font-variant-ligatures: none;
  /* Smoothing */
  text-rendering: optimizelegibility;
  text-shadow: rgba(0, 0, 0, 0.01) 0 0 1px;
}

body {
  text-rendering: optimizelegibility;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  overflow-x: hidden;
  position: relative;
}

input,
select,
textarea,
button {
  -webkit-tap-highlight-color: transparent;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

body {
  background-color: black;
  color: white;
  text-align: center;
}

button {
  background-color: black;
  border: 1px solid white;
  color: white;
  padding: 10px 30px;
}
button:hover {
  background-color: white;
  color: black;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.maze {
  margin: 30px auto;
  width: 400px;
  height: 400px;
  outline: 2px solid white;
  position: relative;
}
.maze__announcer {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.maze__encounter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.player {
  width: 30px;
  height: 30px;
  background-color: blue;
  position: absolute;
}

.game {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}
.game * {
  flex-grow: 1;
}
.game__input {
  height: 35px;
  width: 100%;
  max-width: 400px;
  text-align: center;
  font-size: 24px;
}
.game__interface {
  flex: 1;
}
.game__stats {
  flex: 1;
}

.doors {
  position: absolute;
  background-color: black;
}
.doors::before, .doors::after {
  content: "";
  position: absolute;
  transition: all 0.3s ease-in-out;
  background-color: white;
}
.doors--north, .doors--south {
  width: 80px;
  height: 4px;
}
.doors--north::before, .doors--north::after, .doors--south::before, .doors--south::after {
  height: 40px;
  width: 2px;
}
.doors--north {
  top: -2px;
  left: 50%;
  transform: translateX(-50%);
}
.doors--north::before {
  top: 1px;
  left: calc(50% - 41px);
  transform-origin: top center;
}
.doors--north::after {
  top: 1px;
  left: calc(50% + 40px);
  transform-origin: top center;
}
.doors--south {
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%);
}
.doors--south::before {
  bottom: 1px;
  left: calc(50% - 41px);
  transform-origin: bottom center;
}
.doors--south::after {
  bottom: 1px;
  left: calc(50% + 40px);
  transform-origin: bottom center;
}
.doors--east, .doors--west {
  height: 80px;
  width: 4px;
}
.doors--east::before, .doors--east::after, .doors--west::before, .doors--west::after {
  width: 40px;
  height: 2px;
}
.doors--east {
  top: 50%;
  right: -2px;
  transform: translateY(-50%);
}
.doors--east::before {
  top: calc(50% - 41px);
  right: 1px;
  transform-origin: center right;
}
.doors--east::after {
  top: calc(50% + 40px);
  right: 1px;
  transform-origin: center right;
}
.doors--west {
  top: 50%;
  left: -2px;
  transform: translateY(-50%);
}
.doors--west::before {
  top: calc(50% - 41px);
  left: 1px;
  transform-origin: center left;
}
.doors--west::after {
  top: calc(50% + 40px);
  left: 1px;
  transform-origin: center left;
}
.doors.closed.doors--north::before {
  transform: rotate(-90deg);
}
.doors.closed.doors--north::after {
  transform: rotate(90deg);
}
.doors.closed.doors--south::before {
  transform: rotate(90deg);
}
.doors.closed.doors--south::after {
  transform: rotate(-90deg);
}
.doors.closed.doors--east::before {
  transform: rotate(-90deg);
}
.doors.closed.doors--east::after {
  transform: rotate(90deg);
}
.doors.closed.doors--west::before {
  transform: rotate(90deg);
}
.doors.closed.doors--west::after {
  transform: rotate(-90deg);
}

.hero {
  position: absolute;
  transition: all 1s ease-in-out;
}
.hero.from-right {
  right: -15%;
  top: 42%;
  animation: moveFromRight 1s ease-in-out forwards;
  --end-left: initial;
  --end-top: 42%;
  --end-right: 10%;
  --end-bottom: initial;
}
@keyframes moveFromRight {
  0% {
    right: -15%;
    top: 42%;
  }
  100% {
    right: 10%;
    top: 42%;
  }
}
.hero.from-left {
  left: -15%;
  top: 42%;
  animation: moveFromLeft 1s ease-in-out forwards;
  --end-left: 10%;
  --end-top: 42%;
  --end-right: initial;
  --end-bottom: initial;
}
@keyframes moveFromLeft {
  0% {
    left: -15%;
    top: 42%;
  }
  100% {
    left: 10%;
    top: 42%;
  }
}
.hero.to-centre {
  animation: moveToCentre 1s ease-in-out forwards;
}
@keyframes moveToCentre {
  100% {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}